<template>
    <div>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" :showCheckBox="true" ModuleName="Dynamic_Reporting" SubModuleCode="Dynamic_Reporting" IdentityColumn="Report_emailId"
            HeaderText="Schedule Listing" :ListData="ExpenseData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata">
        </tg-list>
        <modalscheduleview v-if="showschedulereportview" @close="showschedulereportview = false" :EditScheduleData="EditScheduleData" @RefreshScheduleListing="RefreshScheduleListing">
        </modalscheduleview>
    </div>
</template>
<script>
import ReportDataService from "@/services/DataService";
import Schema from "@/views/Reporting/EmailScheduleSchema";
import modalscheduleview from '../../components/Common/Modal/modalschedule.vue';

export default {
     components: {
        modalscheduleview
    },
   async created() {
        var ths = this;
        ths.privilegeParams = `controller=Report&action=`;
       await ths.FetchData();
    },
    data() {
        return {
            showschedulereportview:false,
            ExpenseData: [],
            TotalRecords: null,
            listheaderbuttons: Schema.buttons,
            PageSize: 10,
            PageNumber: 1,
            SortBy: '',
            SortExp: '',
            isLoading: false,
            leftSearchFields: Schema.leftsearchSchema,
            privilegeParams: null,
            listActions: Schema.listActions,
            Headers: [
                {
                    COLUMN_NAME: "REPORTNAME",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Report Name",
                    DISPLAY_ORDER: 1,
                }, 
                {
                    COLUMN_NAME: "Subject",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Subject",
                    DISPLAY_ORDER: 1,
                },{
                    COLUMN_NAME: "MODULENAME",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Module Name",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "CREATED_BY",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Created By",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "SLOT_DISPLAY",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Slot Display",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "Frequency",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Frequency",
                    DISPLAY_ORDER: 1,
                },
                {
                    COLUMN_NAME: "Send_To",
                    DATA_TYPE: "text",
                    SORTABLE: false,
                    VISIBLE: true,
                    DISPLAY_NAME: "Send To",
                    DISPLAY_ORDER: 1,
                }

            ],
        }
    },
    methods: {
        RefreshScheduleListing: async function(){
           await this.FetchData();
        },
        FetchData:async function () {
            var vm = this;
            vm.isLoading = true;
            const params = vm.getRequestParams(this.pageNumber, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.searchCondition);
            params.pageSize = vm.PageSize;
            params.pageNumber = vm.PageNumber;
            if (vm.searchCondition != '' && vm.searchCondition != undefined) {
                params.ReportName = $(".search").val();
            }else{
                params.ReportName = null;
            }
            if (vm.PageNumber) { params.pageNumber = vm.PageNumber; }
            else { vm.PageNumber = params.pageNumber = 1; }
            if (typeof vm.isPaged != 'undefined') { params.isPaged = vm.isPaged; }
            else { vm.isPaged = vm.isPaged = 1; }
            // params.isPaged = vm.isPaged;
            params.sortBy = vm.SortBy;
            params.sortExp = vm.SortExp;
           await ReportDataService.EmailScheduleListing(params)
                .then(function (response) {
                    if(response.data.Data.length > 0 && response.data.Data != []){
                    vm.ExpenseData = response.data.Data;
                    vm.ExpenseData.forEach(exitem=>{
                        if(exitem.ACCESS == 1 || exitem.ACCESS == 2){
                            exitem.isCheckBoxDisabled = true;
                        }else{exitem.isCheckBoxDisabled = false}
                    })
                    vm.TotalRecords = response.data.Data[0].TOTAL_RECORDS}
                    else{
                        vm.ExpenseData=[];
                        vm.TotalRecords=0;
                        vm.isLoading = false;
                    }
                    setTimeout(function () {
                        vm.ResponsiveDataTable('tablelistingdata')
                    }, 500);
                    vm.isLoading = false;
                });
        },
        getRequestParams: function (pageNumber, pageSize, sortBy, sortExp, isPaged, searchcondition) {
            let params = {};
            if (pageNumber) { params["pageNumber"] = pageNumber; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            // if (isPaged) { params["is_paged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = row.ACCESS != 1 ? true : false;
                        break;
                    default:
                        action.isVisible = true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        leftCommonSearch:async function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
          await  this.FetchData();
        },
        pagerMethod:async function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
           await this.FetchData();
        },
        commonHeaderButtonClick: async function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    await this.commonDeleteData();
                    break;
                case "BackToMainList":
                    this.BackToMainList();
                    break;
                case 'ScheduledHistory':
                    this.ScheduledHistory();
                    break;
            }
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            switch (callbackfunction) {
                case 'EditScheduleEmail':
                    vm.EditScheduleEmail(item);
                    break;
            }
        },
        sortdata:async function (s) {
            var vm = this;
            var SortDir = "sort tb_headerSortDown";
            vm.SortBy = s;
            if (s === vm.SortBy) {
                if (vm.SortExp == "") {
                    vm.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (vm.SortExp == "ASC") {
                    vm.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    vm.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
           await vm.FetchData();
            $("#th-" + s + '  span:first-child').attr("class", SortDir);
        },
        commonDeleteData: async function (event) {
            var vm = this;
            ReportDataService.CheckPrivilege(vm.privilegeParams+'ScheduledReportDelete').then((response)=>{
                if(response.data){
                    var val = "";
                    $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                        if (val.length > 0)
                            val += ',';
                        val += $(this).val();
                    });
                    if (val.length > 0) {
                        vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("ScheduledReport")), function () {
                            var postJSON = {
                                ids: val,
                                moduleName: "Dynamic_Reporting",
                                subModuleCode: "Dynamic_Reporting",
                                refCode: "Dynamic_Schedule_Report"
                            };
                            var objectPostString = JSON.stringify(postJSON);
                            ReportDataService.CommonDeleteData(objectPostString).then( async function (response) {
                                if (response.status == 200) {
                                    $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                                    $("#DeleteMultiple").addClass("disabled");
                                    vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('ScheduledReport')]), "success", true, vm.$t("Alert"));
                                    vm.PageNumber = parseInt(vm.PageNumber) - 1;
                                    if (vm.PageNumber <= 0) {
                                        vm.PageNumber = 1;
                                    }
                                await vm.FetchData();
                                }
                                else {
                                    vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Report')]), "success", true, vm.$t("Alert"));
                                }

                            },);
                        });
                    }
                    else {
                        ShowAlert(vm.$t("Selectanyrecordtodelete"));
                    }
                }
                else{
                    vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                    return false;
                }
            });

        },
        EditScheduleEmail:function(value){
            var vm = this;
            ReportDataService.CheckPrivilege(vm.privilegeParams+'ScheduledReportUpdate').then((response)=>{
                if(response.data){
                    vm.EditScheduleData = value;
                    vm.showschedulereportview = true;
                    $("#modalscheduleview").modal('show');
                    setTimeout(function () {
                        $("#modalscheduleview").modal('show');
                        setTimeout(function () {
                            vm.SetBootstraptoolTip();
                        }, 200);
                    }, 1000);
                }
                else{
                    vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                    return false;
                }
            });
        },
        BackToMainList:function(){
            var vm = this;
            vm.$router.push({ name: 'CustomReports' })
        },
        ScheduledHistory:function(){
            var vm = this;
            vm.$router.push({ name: 'ScheduledHistory'})
        }
        
    }
}
</script>
